.header {
  margin: 0 auto;
  height: 88px;
  max-width: 1920px;
  background: #1c1c21;
  border-radius: 0px;
  box-shadow: 0px 0px 1px 0px #0000000a;
  box-shadow: 0px 4px 8px 0px #0000000a;
  box-shadow: 0px 16px 24px 0px #0000000a;
  box-shadow: 0px 24px 32px 0px #0000000a;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: center;
  height: 88px;
  width: 1280px;
  margin-left: auto;
  margin-right: auto;
}

.logo {
  margin: 24px 288px 24px 112px;
}

 .activeStyle  {
  text-decoration: none;
  display: flex;
  flex-direction: row nowrap;
  align-items: center;
  gap: 8px;
  background: #1c1c21;
  width: 179px;
  height: 56px;
  border: none;
  color: white;
};

.noActiveClassName  {
  text-decoration: none;
  display: flex;
  flex-direction: row nowrap;
  align-items: center;
  gap: 8px;
  background: #1c1c21;
  width: 179px;
  height: 56px;
  border: none;
  color: #8585;
};
.link{
  text-decoration: none;
}